*{padding: 0; margin: 0;}
header{
    background: url(/Portfolio/static/media/header2.c3903e08.jpg) no-repeat;
    height: 940px;
}
.containerHeader nav {
    display: grid;
    grid-template-columns:repeat(4, 1fr);
    grid-auto-rows: minmax(100px, auto);
}
.headerMenu{
    justify-items:center;
}
.headerMenuLi {
    font-family: consolas;
    color: rgb(245, 222, 179);
    list-style-type: none;
    font-size: 2.1em;
    text-decoration: none;
    line-height: 1;
    transition: 0.5s;
}
.headerMenuLi1, .headerMenuLi2, .headerMenuLi3, .headerMenuLi4{
    align-self: start;
    
}
.headerMenu:hover .headerMenuLi{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: .2;
    -webkit-filter: blur(5px);
            filter: blur(5px);
}
.headerMenu .headerMenuLi:hover{
    -webkit-transform: scale(2);
            transform: scale(2);
    opacity: 1;
    -webkit-filter: blur(0px);
            filter: blur(0px);
}
 .headerMenuLi:after{
    display: block;
	position: absolute;
	left: 0; 
	width: 0;
	height: 2px; 
	background-color: rgb(245, 222, 179); 
	content: "";
	transition: width 0.6s ease-out;
}
 .headerMenuLi:hover:after,
 .headerMenuLi:focus:after{
    width: 100%;
}
.infoHeader{
    font-family: consolas;
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: minmax(100px, auto);
}
.infoHeader .boxInfoHeader{
    position: relative;
    width: 450px;
    height: 450px;
    text-align: center;
    overflow: hidden;
}
.infoHeader .boxInfoHeader .imgHeaderKolbik{
    position: relative;
    width: 100%;
    height: 100%;
    background: url(/Portfolio/static/media/Kolbik.43e1c4ee.jpg);
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: 0 0 !important;
}
.infoHeader .boxInfoHeader .overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}
.infoHeader .boxInfoHeader .overlay2{
    position: absolute;
    top: 0;
    left: -150%;
    width: 120%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    -webkit-transform: skewX(-30deg);
            transform: skewX(-30deg);
    transition: .5s;
}
.infoHeader .boxInfoHeader:hover .overlay2{
    left: -10%;
}
.infoHeader .boxInfoHeader .Headertext{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 80%;
    color: black;
    box-sizing: border-box;
    padding: 25px;
    z-index: 1000;
    text-shadow:5px 5px 10px black ;
}
.infoHeader .boxInfoHeader .Headertext span:nth-child(1){
    position: absolute;
    top: 0;
    left: -20px;
    width: 0;
    height: 1px;
    background-color: white;
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: .7s ease-in-out;
    transition-delay: .7s ;
}
.infoHeader .boxInfoHeader .Headertext span:nth-child(2){
    position: absolute;
    top: -20px;
    left: 0px;
    width: 1px;
    height: 0;
    background-color: white;
    -webkit-transform-origin: top;
            transform-origin: top;
    transition: .7s ease-in-out;
    transition-delay: .1.1s ;
}
.infoHeader .boxInfoHeader .Headertext span:nth-child(3){
    position: absolute;
    bottom: 0;
    right: -20px;
    width: 0;
    height: 1px;
    background-color: white;
    -webkit-transform-origin: right;
            transform-origin: right;
    transition: .7s ease-in-out;
    transition-delay: .9s ;
}
.infoHeader .boxInfoHeader .Headertext span:nth-child(4){
    position: absolute;
    bottom:-20px;
    right: 0;
    width: 1px;
    height: 0;
    background-color: white;
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: .7s ease-in-out;
    transition-delay: .1.3s ;
}
.infoHeader .boxInfoHeader:hover .Headertext span:nth-child(1),
.infoHeader .boxInfoHeader:hover .Headertext span:nth-child(3){
    width: 110%;
}
.infoHeader .boxInfoHeader:hover .Headertext span:nth-child(2),
.infoHeader .boxInfoHeader:hover .Headertext span:nth-child(4){
    height:150% ;
}
.infoHeader .boxInfoHeader .Headertext h2{
    position: relative;
    color:rgb(245, 222, 179); 
    font-size: 2em;
    text-transform: uppercase;
    margin-bottom: 0;
    letter-spacing: 1px;
    opacity: 0;
    transition: .5s;
    transition-timing-function: linear;
    -webkit-transform: translateY(-10px) ;
            transform: translateY(-10px) ;
}
.infoHeader .boxInfoHeader:hover .Headertext h2{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    transition-delay: 1.6s;
}
.infoHeader .boxInfoHeader .Headertext p{
    color:rgb(245, 222, 179); 
    line-height: 1.2;
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    transition: .5s;
    transition-timing-function: linear;
}
.infoHeader .boxInfoHeader:hover .Headertext p{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    transition-delay: 1.6s;
}
