    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    box-shadow: 0 0 20px #000, 0 0 0 1000px rgba(210,210,210,.4);
}
img[tabindex="0"]:focus ~ * {
    pointer-events: none;
    cursor: zoom-out;
  }
@media screen and (max-width: 960px) {
    .globalPortfolioContainer p{
        color: blanchedalmond;
        font-size: 1.7em;
        text-align: center;
    }

}
@media screen and (max-width: 870px) {
    .PortfolioContainer{
        grid-template-columns: repeat(1, 1fr);
    }

}
@media screen and (max-width: 440px) {
    .globalPortfolioContainer h1{
        font-size: 2.5em;
        text-align: center;
        color: blanchedalmond;
        padding: 10px;
    }
    .imgContainer img[tabindex="0"]{
        width: 250px;
    }
}

footer{
    width: 100%;
    background-color: blanchedalmond;
    font-family: consolas;
}
.h1Footer{
    text-align: center;
    color: rgb(5, 5, 5);
    font-size: 35px;
    padding: 10px;
}
ul.ulFooter{
    display: grid;
    grid-template-columns:repeat(4, 1fr);
    grid-auto-rows: minmax(100px, auto);
    justify-items:center;
}
.liFooter{
    list-style-type: none;  
   font-size: 21px;
   transition:0.6s ease-out;
}
.liFooter:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.liFooter .linkFooter{
    text-decoration: none;
    color: black;
}
.linkFooter img{
    width: 30px;
}
@media screen and (max-width: 1188px) {
  
    ul.ulFooter{
        grid-template-columns:repeat(2, 1fr);
